@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.GraphView{width:100%;height:100%}.GraphView #tree-container{width:100%;height:100%;background-color:#f8f8f8}.GraphView #tree-container #tree-container-svg{width:100%;height:600px;cursor:-webkit-grab;cursor:grab}.GraphView #tree-container #tree-container-svg circle{cursor:cell}

section.DocTable{padding:20px;overflow-y:scroll}section.DocTable table{font-size:14px;margin:30px 0px;border-color:#1C1C1C;color:#2E1165}section.DocTable table thead.header{border:none;background-color:#fff}section.DocTable table .field-name{font-family:'Roboto Mono', monospace;font-weight:700;color:#F7734F}section.DocTable h1{font-family:'Raleway', sans-serif;margin:40px 0px}section.DocTable h5{line-height:1.5em;font-weight:300}section.DocTable .msg-name-box{margin-top:40px;margin-bottom:5px;font-size:2em}section.DocTable .msg-name-box .msg-name-tag{background-color:#222;padding:4px;border-top-left-radius:2px;border-bottom-left-radius:2px;border:1px solid #222;color:#fff}section.DocTable .msg-name-box .msg-name{color:#2E1165}section.DocTable .msg-description{font-weight:300}section.DocTable .msg-table{border-bottom:1px dotted #ddd}section.DocTable .enum-start-point{margin-top:90px;border-bottom:1px dotted #888;color:#888}

.DocMainContent .hljs,.DocMainContent .hljs-attr{color:#1C1C1C}.DocMainContent .hljs-attr{font-weight:600;color:#42278E}.DocMainContent .hljs-string{color:#F7734F}.DocMainContent .hljs-keyword,.DocMainContent .hljs-title,.DocMainContent .hljs-built_in{color:#42278E}.DocMainContent .hljs-title{font-weight:600}.DocMainContent .vert-align{width:100%}.DocMainContent .vert-align .like-share-button{font-size:0.9em;text-align:center;margin-top:10px}.DocMainContent .shadow-box{box-shadow:0px 3px 4px 2px #00000019;padding:5px 10px;margin:10px 0px;border-radius:8px}.DocMainContent .like-share-box{width:300px;margin:10px auto}.DocMainContent .like-share-box img{height:30px;width:30px;display:block;margin:auto auto;transition:all .2s ease-in-out;cursor:pointer}.DocMainContent .like-share-box img:hover{transform:scale(1.2)}

.DocContentList{position:fixed;padding:0px 0px 10px 10px;font-size:0.8em;border-left:2px solid #42278E;font-family:'Roboto Mono', monospace}.DocContentList .index{margin-top:20px}.DocContentList .index ul{margin:0;padding:0;list-style:none;overflow-y:scroll;height:400px}.DocContentList .index ul a{text-decoration:none;color:#2E1165}.DocContentList .index ul li{margin:6px 0px}.DocContentList h5{margin-bottom:5px;margin-top:0px}.DocContentList .shadow-box{box-shadow:0px 3px 4px 2px #00000019;padding:5px 10px;margin:10px 0px;border-radius:8px}.DocContentList .hori-align img{width:16px;margin:auto auto;vertical-align:top;margin-top:5px}.DocContentList .hori-align .content{display:inline-block;margin-left:10px;width:100px;font-size:1em;margin-top:5px;text-align:center;font-family:'Roboto Mono', monospace;font-size:1.2em;text-align:left}.DocContentList .hori-align .version{font-size:0.9em;font-family:'Roboto Mono', monospace}.DocContentList .hori-align .license{font-size:0.9em;font-family:'Roboto Mono', monospace}.DocContentList .hori-align .last-updated{font-size:.7em}

.DocContent{padding-left:10px}

section.DocSearch{height:100%;width:100%;background-color:#2e1165;padding:20px}section.DocSearch .doc-search{margin-top:15px;width:100%}section.DocSearch .logo-div{border-bottom:7px solid #fff}section.DocSearch .navigation{margin-top:30px}section.DocSearch .collapsible{background-color:transparent;padding-top:0.2rem;color:#fff;font-size:16px}section.DocSearch .collapsible a{color:#fff;text-decoration:none;padding:0px;margin:0px}section.DocSearch .collapsible .card-header{padding:0px}section.DocSearch .collapsible .card-body{padding:0rem;font-size:14px}section.DocSearch .collapsible .card-body ul{margin-bottom:0rem;padding-left:1rem}section.DocSearch .collapsible .card-body li{list-style-type:none;margin-top:5px}section.DocSearch .collapsible .card-body li a:hover{color:#F7734F}

.Footer{z-index:10;padding:30px;position:relative;background-color:#2F176C}.Footer .section{margin-bottom:20px}.Footer h5{color:#F7734F;font-weight:600;margin:0px;margin-bottom:2px;font-size:1em}.Footer ul,.Footer li{list-style-type:none;margin:0;padding:0;font-size:0.9em}.Footer ul a,.Footer li a{color:#fff}.Footer li{margin-bottom:3px}.Footer small{display:block;margin:2px 0px;color:#fff;font-family:"Roboto",sans-serif;font-size:0.6em}.Footer .heart-color{color:#D43C67}

section.DocPage{width:100%;height:100%;color:#000}section.DocPage .left-panel{width:20%;height:100%;position:fixed;display:inline-block}section.DocPage .right-panel{width:79.96%;margin-left:20.01%;display:inline-block}section.DocPage h1{font-size:4.5em;font-weight:700;margin:40px 0px;color:#2E1165}section.DocPage h5{line-height:1.5em;font-weight:300}section.DocPage .full-width-height{width:100%}section.DocPage .nav-path{padding-left:5px;margin-top:20px;font-size:1em;font-family:'Roboto Mono', monospace;color:#F7734F}section.DocPage .registry-name{padding:5px 15px;margin:20px 20px 10px 5px;font-size:1.5em;border-bottom:7px solid #2E1165;color:#2E1165;font-weight:700;text-align:right}section.DocPage .standard-description{margin-bottom:60px}section.DocPage .nav-tabs{border-bottom:1px solid #2E1165}section.DocPage .nav-item a{color:#2E1165}section.DocPage .nav-item a:hover{color:#fff;background-color:#2E1165;border-color:#2E1165}section.DocPage .nav-item a:active{color:#fff}section.DocPage .nav-link.active{color:#fff;background-color:#2E1165;border-color:#2E1165}section.DocPage .hljs{background:none}

html,body{height:100%;width:100%;font-family:'Mulish', sans-serif;overflow:visible}#root,.App{height:100%;width:100%}.nopadding{padding:0 !important;margin:0 !important}

